.knowledge-main {
  position: absolute;
  padding: 20px;
  top: 42px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  background: #fff;
}

.knowledge-tags {
  padding: 20px;
  margin: 10px 0;
  background: #f6f6f6;
  font-size: 14px;
  line-height: 34px;
  color: #333;

  label {
    display: inline-block;
    padding: 0 10px;
    margin: 0 5px;
    background: #fff;
    border-radius: 4px;
    color: #999;
    cursor: pointer;

    &.active {
      color: #fff;
      background: #50d1c2;
    }
  }
}

.knowledge-li {
  position: relative;
  margin: 10px 0;
  padding: 20px;
  border: 1px solid #e7e7e7;

  a.href {
    text-decoration: none;
  }
}

.knowledge-sign {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 14px;
  color: #ffb96c;
}

.knowledge-title {
  padding: 10px 0 20px;
  font-size: 18px;
}

.knowledge-sub {
  font-size: 14px;
  color: #999;

  .icon {
    display: inline-block;
    width: 12px;
    height: 14px;
    margin-right: 5px;
    vertical-align: middle;
    background: url(../images/label.png) no-repeat;
    background-size: contain;
  }

  span {
    margin: 0 10px;
  }

  a.href+span {
    margin-left: 30px;
  }
}

.knowledge-content {
  margin: 20px 0;
  font-size: 14px;
  color: #999;
  line-height: 24px;
}

.kl-file {
  max-height: 35px;
  overflow: hidden;
}

.knowledge-file {
  display: inline-block;
  padding: 0 20px;
  margin-bottom: 10px;
  margin-right: 10px;
  border-radius: 15px;
  line-height: 30px;
  font-size: 12px;
  color: #76adea;
  border: 1px solid #ddd;
}

.knowledge-tag-more {
  position: relative;

  // padding-right: 20px;
  .tags-li>& {
    padding-right: 30px;
  }

  &:before,
  &:after {
    content: '';
    position: absolute;
    right: 8px;
    border-width: 6px 6px 0;
    border-style: solid;

    .tags-li:hover & {
      border-width: 0 6px 6px;
    }
  }

  &:before {
    top: 17px;
    border-color: #333 transparent transparent;

    .tags-li:hover & {
      top: 16px;
      border-color: transparent transparent #333;
    }
  }

  &:after {
    top: 16px;
    border-color: #f5f5f5 transparent transparent;

    .tags-li:hover & {
      top: 17px;
      border-color: transparent transparent #f5f5f5;
    }
  }
}

.tags-li {
  position: relative;
}

.tag-dropdown {
  display: none;
  position: absolute;
  z-index: 9;
  top: 40px;
  right: 0;
  white-space: nowrap;
  background: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  min-width: 100%;

  a.tag-dropdown-li {
    display: block;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    padding: 0 10px;

    &:hover {
      background: #f6f6f6;
    }
  }

  .tags-li:hover & {
    display: block;
  }
}

.plate-table {
  margin: 20px;
  border: 1px solid #f0f0f0;
  border-radius: 5px;

  .tag-move {
    display: inline-block;
    width: 18px;
    height: 18px;
    vertical-align: middle;
    background: url(../images/tag-move.png) no-repeat;
    background-size: contain;
  }
}

.plate-row {
  overflow: hidden;
  border-bottom: 1px solid #f0f0f0;
  font-size: 14px;
  line-height: 40px;

  &:last-child {
    border-bottom: 0;
  }

  >div {
    border-right: 1px solid #f0f0f0;

    &:last-child {
      border-right: 0;
    }
  }

  &.plate-content {
    font-size: 12px;

    >div {
      border-right: 0;
    }
  }

  &.addPlate {
    padding: 0 10px;
    background: #f7f7f7;
    font-size: 12px;
  }
}

$plateTable: (name: 50%,
setting: 30%,
sort: 10%,
);

@each $plate,
$width in $plateTable {
  .plate-#{$plate} {
    float: left;
    width: $width;
    padding: 0 10px;
    box-sizing: border-box;
  }
}

.plateInput {
  padding: 5px;
  width: 80%;
  border: 1px solid #50d2c2;
  border-radius: 5px;
}

.knowledge-tag,
.knowledge-tag-add,
.knowledge-tag-default {
  display: inline-block;
  padding: 0 15px;
  line-height: 35px;
  border-radius: 18px;
  min-width: 60px;
  text-align: center;
  box-sizing: border-box;
}

.knowledge-tag {
  position: relative;
  margin-right: 10px;
  border: 1px solid #56d1c2;
  background: #56d1c2;
  color: #fff;
  cursor: pointer;

  span {
    margin-left: 5px;
    font-size: 24px;
    vertical-align: sub;
  }
}

.knowledge-tag-add {
  border: 1px solid #ddd;
  color: #666;

  input[type='text'] {
    width: 60px;
    height: 35px;
    border: 0;
    background: transparent;
    padding: 0;
  }
}

.konwledge-text {
  font-size: 12px;
  color: #666;
}

.knowledge-tag-default {
  margin-right: 10px;
  border: 1px solid #ddd;
  color: #56d1c2;
  cursor: pointer;

  &.active {
    background: #56d1c2;
    color: #fff;
  }
}

.konwledge-form-wrap {
  position: absolute;
  width: 100%;
  top: 51px;
  bottom: 0;
  left: 0;
  overflow: auto;
  padding: 10px 0;
  background: #fff;
  box-sizing: border-box;
}

.enclosure-btn {
  a.btn {
    line-height: 1.5;
  }
}

.file-li {
  width: 60%;
  margin: 0;
  margin-bottom: 2px;
  padding: 5px 10px;
  line-height: 24px;
  overflow: hidden;
  background: #e0fcf9;
  color: #56d1c2;

  a {
    color: #56d1c2;
  }
}

.file-name {
  float: left;
  width: 50%;
}

.file-size {
  float: left;
  width: 10%;
}

.file-progress {
  position: relative;
  float: left;
  margin: 8px 5px;
  width: 30%;
  height: 8px;
  background: #9ff5eb;
  border-radius: 5px;
  box-sizing: border-box;

  .progress-bar {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    max-width: 100%;
    background: #56d1c2;
    border-radius: 5px;
  }
}

.file-remove {
  float: right;
  width: 5%;
  font-size: 24px;
}

.knowledge-detail {
  padding: 20px;
}

.kl-fr {
  margin-top: -8px;
  margin-right: 20px;
}

.kl-btn {
  display: inline-block;
  padding: 0 10px;
  text-align: center;
  border-radius: 5px;
  font-weight: normal;
  font-size: 16px;
  line-height: 34px;
  background: #e0fcf9;
  color: #56d1c2;

  i.icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-size: contain;
    background-repeat: no-repeat;
    vertical-align: sub;

    &.icon-setTop {
      background-image: url(../images/setTop.png);
    }

    &.icon-delete {
      background-image: url(../images/konwDelete.png);
    }

    &.icon-editor {
      background-image: url(../images/editor.png);
    }
  }
}

.manage-info.kl-content {
  top: 55px;
  // left: 0;
  // right: 0;
}

.manage-con-top {
  input.btn-red {
    height: auto;
    border: 1px solid;
  }
}

.file-box+.tag-default {
  margin-top: 15px;
}

.KLM-name {
  font-weight: bold;
  font-size: 20px;
}

.KLM-summary {
  margin-top: 20px;
  font-size: 14px;
  color: #383838;

  >span {
    margin-right: 20px;
  }
}

.avatar {
  float: left;
  width: 40px;

  img {
    width: 40px;
    height: 40px;
  }

  span {
    display: block;
    margin-top: 10px;
    text-align: center;
    font-size: 14px;
    color: #a2a2a2;
  }
}

.KLM-content {
  overflow: hidden;
  margin: 30px 0;
}

.KLM-info {
  margin-left: 60px;
  padding: 5px 15px;
  position: relative;
  border: 1px solid #e7e7e7;

  // border-radius: 5px;
  &:before,
  &:after {
    position: absolute;
    content: '';
    top: 10px;
    border-width: 8px 8px 8px 0;
    border-style: solid;
  }

  &:before {
    left: -8px;
    border-color: transparent #e7e7e7;
  }

  &:after {
    left: -7px;
    border-color: transparent #fff;
  }
}

.KLM-li {
  margin: 10px 0;
  font-size: 14px;
  line-height: 30px;
}

.KLM-bottom {
  padding: 15px 0 10px;
  border-top: 1px solid #e7e7e7;
}

.KL-tips {
  margin: 30px auto;
  text-align: center;

  i {
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-right: 15px;
    background: url(../images/kl-tips.png) no-repeat;
    background-size: contain;
    vertical-align: middle;
  }
}